import React, { Component } from "react";
import { Container, Row } from "react-bootstrap";
import { FaShoppingCart } from "react-icons/fa";
import { Link } from "react-router-dom";
import İtemConsumer from "../context";
import { ImHome } from "react-icons/im";
import { AiFillGithub } from "react-icons/ai";

import { Navbar, Nav, NavItem, NavLink } from "reactstrap";

export default class Navi extends Component {
  render() {
    return (
      <Container>
        <Row>
          <div className="col-12">
            <Navbar color="light" light expand="md">
              <Link to="/">
                <ImHome color="green" size="30px" />
              </Link>

              <Nav className="mr-auto" navbar>
                <NavItem>
                  <NavLink href="https://github.com/ertugrulsarihan">
                    <AiFillGithub
                      style={{ position: "relative", left: "33px" }}
                      color="green"
                      size="33px"
                    />
                  </NavLink>
                </NavItem>
              </Nav>
              <İtemConsumer>
                {(value) => {
                  const length = value.cart.length;
                  return (
                    <Link to="/cart">
                      <FaShoppingCart
                        style={{ position: "relative" }}
                        color="green"
                        size="30px"
                      />
                      {length}
                    </Link>
                  );
                }}
              </İtemConsumer>
              <p className="success">{this.props.count}</p>
            </Navbar>
          </div>
        </Row>
      </Container>
    );
  }
}
