import React, { Component } from "react";

import { İtems } from "./Components/depo";

const İtemContext = React.createContext();

export class İtemProvider extends Component {
  state = {
    items: İtems.items,
    product: İtems.product,
    cart: İtems.cart,
    prices: 0,
    
  };

  deleteMe = card =>{
    if(window.confirm("Are you sure?")){
        const {cart} = this.state;
        cart.forEach((item, index) =>{
            if(item.id === card.id){
                cart.splice(index, 1)
            }
        })
        this.setState({cart: cart});
        this.getTotal();
    }
   
};

reduction = card =>{
  const { cart } = this.state;
  cart.forEach(item =>{
      if(item.id === card.id){
          item.count === 1 ? item.count = 1 : item.count -=1;
      }
  })
  this.setState({cart: cart});
  this.getTotal();
};



   increment=(card)=>{
    const { cart } = this.state;
    cart.forEach(item=>{
      if(item.id ===card.id){
        item.count++;
          }
        })
          this.setState({cart:cart});
          this.getTotal();
    } 

 getTotal=()=>{
  const{cart} = this.state;
  const res = cart.reduce((prev, item) => {
      return prev + (item.price * item.count);
  },0)
  this.setState({prices: res})
 }





   
  addMe = (card) => {
    const {product, cart,prices} = this.state;
    const newPrice=prices + card.price
    const check = cart.every(item =>{
        return item.id !==card.id
    })
    if(check){
        const data = product.filter(product =>{
            return product.id === card.id
        })
        this.setState({cart: [...cart,...data]})
        this.setState({prices:newPrice })
    }else{
        alert("The product has been added to cart.")
    }
  };

  

  render() {
    return (
      <İtemContext.Provider
        value={{
          ...this.state,
          deleteMe: this.deleteMe,
          addMe: this.addMe,
          increment:this.increment,
          reduction:this.reduction
         }}
      >
        {this.props.children}
      </İtemContext.Provider>
    );
  }
}

const İtemConsumer = İtemContext.Consumer;
export default İtemConsumer;
