import { Button, Row } from "reactstrap";
import React, { Component } from "react";
import { BsFillTrashFill } from "react-icons/bs";

import { Container } from "reactstrap";

import İtemConsumer from "../context";

export default class Cart extends Component {
  render() {
    return (
      <İtemConsumer>
        {(value) => {
          const cart = value.cart;

          return (
            <Container>
              <Row>
                <div class="d-flex justify-content-end">
                  <button className="btn btn-primary">
                    Price ={value.prices}$
                  </button>

                  <button className="btn btn-success">Payment</button>
                </div>
              </Row>
              <Row>
                <div className="col-12">
                  {cart.map((item) => (
                    <div
                      key={item.id}
                      class="card mb-3"
                      style={{ maxWidth: "540px" }}
                    >
                      <div class="row g-0">
                        <div class="col-md-4">
                          <img
                            style={{ height: "280px", width: "200px" }}
                            className=""
                            src={item.img}
                            alt="..."
                          />
                        </div>
                        <div class="col-md-8">
                          <div class="card-body">
                            <h5 class="card-text">{item.productname}</h5>
                            <p class="card-text">{item.info}</p>

                            <Row>
                              <div className="d-flex justify-content-end btn-inc">
                                <Button
                                  onClick={() => value.deleteMe(item)}
                                  color="danger"
                                >
                                  <BsFillTrashFill />
                                </Button>
                                <button
                                  onClick={() => value.reduction(item)}
                                  className="btn btn-primary"
                                >
                                  -
                                </button>
                                <div
                                  style={{ width: "100px" }}
                                  className="text-center"
                                >
                                  <h4>{item.count}</h4>
                                </div>
                                <button
                                  onClick={() => value.increment(item)}
                                  className="btn btn-primary"
                                >
                                  +
                                </button>
                              </div>
                            </Row>
                          </div>
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              </Row>
            </Container>
          );
        }}
      </İtemConsumer>
    );
  }
}
